.link__container {
  display: block;
  margin-right: 96px;

  @include vp-1023 {
    margin-right: 0;
  }

  @include vp-767 {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: -25px;
      right: -25px;
      height: 1px;

      background-color: $color-tints-shalimar-line;
    }
  }
}

.link__container[open] > summary::before {
  @include vp-767 {
    transform: rotate(0deg);
  }
}

.link__container summary::-webkit-details-marker {
  display: none;
}

.link__container summary::-moz-list-bullet {
  list-style-type: none;
}

.link__container summary::marker {
  display: none;
}

.link__container summary {
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 20px;

  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  font-family: $ff-roboto-condensed;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  cursor: pointer;
  pointer-events: none;

  @include vp-767 {
    position: relative;
    pointer-events: all;
    width: 100%;
    margin-bottom: 0;
    padding-top: 22px;
    padding-bottom: 22px;

    &::after {
      content: "";
      position: absolute;
      top: 44%;
      right: 2px;

      width: 15px;
      height: 1px;

      background-color: $color-tints-alice-blue;
    }

    &::before {
      content: "";
      position: absolute;
      top: 44%;
      right: 2px;

      width: 15px;
      height: 1px;

      background-color: $color-tints-alice-blue;
      transform: rotate(90deg);
    }
  }

  &:focus {
    outline: 0;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.3),
      inset 0 0 2px rgba(0, 0, 0, 0.3);
  }
}

.link__container ul {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @include vp-767 {
    margin-top: -6px;
  }
}

.link__container li {
  margin-top: 6px;

  @include vp-1023 {
    margin-top: 0;
    margin-bottom: 6px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  @include vp-767 {
    &:last-of-type {
      padding-bottom: 18px;
    }
  }

  &:first-of-type {
    margin-top: 0;
  }
}

.link__container a {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  font-family: $ff-ptmono;
  color: $color-tints-shalimar;

  @include vp-767 {
    font-size: 14px;
  }

  &:hover {
    background-color: $color-deep-sky-blue;
  }

  &:active {
    background-color: $color-deep-sky-blue;
    color: $color-black-pearl;
  }

  &:focus {
    background-color: $color-deep-sky-blue;
    outline: none;
  }
}

.link__container + details {
  @include vp-1023 {
    margin-right: 13px;
  }

  @include vp-767 {
    margin-right: 0;
  }
}

.link__wrapper {
  display: flex;

  @include vp-1023 {
    flex-direction: column;
  }
}

.link__wrapper li {
  max-width: 220px;
  margin-right: 30px;
}

.link__container-lost {
  margin-right: 0;
}
